// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// start header //
header {
  @extend %header;
}
/* End header */
/* Start Landing Section */
.landing {
  display: block;
  height: 380px;
  background-image: url("../images/mobile-landing.png");
  background-size: cover;
  background-position: top center;
  @include breakpoint(laptop) {
    min-height: 100vh;
    background-image: url("../images/landing.png");
  }
}

/* End Landing Section */

/* End Contact Section */

.contact {
  padding-top: $section-padding-md ;
  text-align: center;
  @include breakpoint(laptop) {
    background-image: url("../images/contact-bg.png");
    background-position: top center;
    background-size: cover;
    height: 497px;
    position: relative;
    top: -115px;
  }
  h2 {
    @extend %section_header-4;
  }
  p {
    font-size: .875rem;
    padding-top: $txt-padding-md;
    color: var(--gray-light-clr);
    span {
      display: none;
    }
  }
  .social-icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: $txt-margin-md auto;
    gap: $txt-padding-sm;
    max-width: 600px;
    a {
      width: 3rem;
      height: 3rem;
      img {
        transition: $transition;
      }
      &:hover img {
        transform: translateY(-.5rem);
        filter: saturate(1.5);
      }
    }
  }
  @include breakpoint(tablet) {
    p {
      font-size: 1rem;
      span {
        display: inline-block;
      }
    }
    .social-icons {
      a {
        width: 4rem;
        height: 4em;
      }
    }
  }
}

/* End Contact Section */

/* Start features Section */

.features {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  h2 {
    @extend %section_header-4;
  }
  .features-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
    gap: $txt-padding-lg;
    margin-top: $txt-margin-lg;
    .feature-item {
      position: relative;
      text-align: center;
      padding: $txt-padding-lg $txt-padding-md;
      border-radius: $border-radius-lg;
      border: 1px solid var(--black-light-clr);
      box-shadow: $box-shadow;
      &:hover {
        border-color: var(--primary-light-clr);
        background-color: var(--primary-light-clr);
        img:first-child {
          animation: scale 0.4s ease-in-out alternate;
        }
        img {
          filter: grayscale(0);
        }
        h3, p {
          color: var(--white-clr);
        }
      }
      img {
        filter: grayscale(100%);
      }
      img:first-child {
        margin-bottom: $txt-padding-sm;
      }
      img:last-child{
        position: absolute;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
      }
      h3 {
        padding-bottom: $txt-padding-md;
        text-transform: capitalize;
        color: var(--purple-dark-clr);
      }
      p {
        font-size: .875rem;
        line-height: $line-height-lg;
        color: var(--gray-dark-clr);
      }
    }
  }
}

/* End features Section */

/* Start Video Section */

.video {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  background-image: url("../images/video-bg.png");
  background-position: top center;
  background-size: cover;
  min-height: 790px;
  .container {
    position: relative;
    padding-top: $section-padding-md ;
    padding-bottom: $section-padding-md ;
    .img-holder{
      position: relative;
      top: 20px;
      height: 360px;
      border-radius: $border-radius-lg;
      overflow: hidden;
      background-image: url("../images/video.png");
      background-position: top center;
      background-size: cover;
      @include breakpoint(laptop) {
        max-width: 450px;
        top: 0;
        left: $txt-margin-lg;
        z-index: 2;
      }
      .video-play-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        &:before,
        &::after {
          content: "";
          position: absolute;
          top: 3px;
          left: 2px;
          width: 50px;
          height: 50px;
          z-index: -1;
          border-radius: $circle;
          animation: pulse 3s infinite;
          background-color: var(--white-light-clr);
        }
        &::after {
          animation-delay: 0.4s;
        }
      }
      svg {
        cursor: pointer;
        border-radius: $circle;
        padding: $txt-margin-md;
        color: var(--primary-dark-clr);
        background-color: var(--white-clr);
      }
    }
    .text{
      text-align: center;
      padding: $txt-margin-lg 0 $txt-margin-md;
      border-radius: $border-radius-lg;
      background-color: var(--primary-dark-clr);
      @include breakpoint(laptop) {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
        text-align: right;
        padding: 4.375rem $txt-margin-lg;
      }
      @include breakpoint(desktop) {
        padding: 4.375rem $txt-margin-xlg;
      }
      p {
        display: inline-block;
        font-size: .875rem;
        letter-spacing: -0.5px;
        color: var(--white-clr);
        @include breakpoint(tablet) {
          font-size: 20px;
        }
        @include breakpoint(desktop) {
          font-size: 25px;;
        }
      }
      img {
        display: inline-block;
        transition: $transition;
      }
      @include breakpoint(desktop) {
        &:hover img {
          animation: round 2s ease-in-out alternate;
        }
      }
    }
    video {
      display: none;
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-height: 280px;
      max-width: 100%;
      margin: 0 auto;
      z-index: 11;
      border-radius: $border-radius-lg;
    }
  }
}

/* End Video Section */

/* Start About Us Section */
.about-us {
  padding-top: $section-padding-md ;
  padding-bottom: $section-padding-md ;
  h2 {
    @extend %section_header-5;
  }
  .time-line {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(325px, 1fr));
    gap: $txt-padding-lg;
    .time-line-item {
      position: relative;
      text-align: center;
      box-shadow: $box-shadow;
      border-radius: $border-radius-lg$border-radius-lg 0 0;
      margin: $txt-margin-md 0;
      .img-holder {
        display: flex;
        align-items: center;
        font-weight: 800;
        justify-content: space-between;
        padding: $txt-padding-md $txt-padding-sm;
        border-radius: $border-radius-lg $border-radius-lg 0 0;
        span {
          font-size: 45px;
          letter-spacing: 3px;
          color: var(--white-light-clr);
        }
      }
      > span {
        position: relative;
        top: -25px;
        font-weight: bold;
        font-size: 1rem;
        padding: $txt-margin-md;
        text-transform: capitalize;
        border-radius: $border-radius-lg;
        background-color: var(--white-clr);
        font-family: 'Barlow Condensed', sans-serif;
        span {
          font-weight: 900;
          font-size: 2rem;
          letter-spacing: -1px;
        }
      }
      p {
        font-size: .8125rem;
        line-height: $line-height-lg;
        color: var(--purple-light-clr);
        padding: 0 $txt-padding-sm $txt-padding-lg;
      }
    }
    .img-holder:first-child {
      background-color: var(--blue-light-clr);
    }
    .img-holder:last-child {
      background-color: var(--green-light-clr);
    }
  }
}

/* Start Footer section */
// footer {
//   @extend %footer;
// }
/* End Footer section */

/* Start Animation */
@keyframes scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes pulse {
  0%{
    transform: scale(1.3)
  }
  20% {
    transform: scale(2);
    opacity: 0.5;
  }
  60% {
    transform: scale(1);
    opacity: 0
  }
}
@keyframes round {
  50%{
    transform: translateX(80px) rotate(180deg);
  }
  100% {
    transform: translateX(0) rotate(360deg);
  }
}
/* End Animation */
